<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="角色名称：" required>
          <el-input style="width:400px;" v-model="form.roleName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="角色描述：">
          <el-input style="width:400px;" type="textarea" v-model="form.roleDescription" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="菜单授权：" required>
          <el-tree
            :data="tree"
            show-checkbox
            node-key="id"
            ref="tree"
            highlight-current
            :default-expanded-keys="openIds"
            :default-checked-keys="openIds"
            :props="defaultProps">
          </el-tree>
        </el-form-item>
        <div class="flex-row-center">
          <el-button @click="cancle">取消</el-button>
          <el-button type="primary" :loading="addLoading" @click="onSubmit">编辑</el-button>
        </div>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findAll , update ,findById} from '@/api/systemSetting/roleManage'
export default {
  data(){
    return{
      form:{},
      tree: [],
      defaultProps: {
        children: 'list',
        label: 'name'
      },
      addLoading:false,
      openIds:[]
    }
  },
  created(){
    this.getTree()
    this.getData()
  },
  methods:{
    getData(){
      findById({id:this.$route.query.id}).then(res=>{
        console.log('角色详情',res)
        if(res.code == 200){
          this.form = res.data
          this.openIds = res.data.menuIds
        }
      })
    },
    getTree(){
      findAll().then(res=>{
        console.log('目录树',res)
        if(res.code == 200){
          this.tree = res.data
        }else{
          this.$message.error(res.message);
        }
      })
    },
    cancle(){
      this.$confirm('确定放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({path:'/systemSetting/roleManage'})
      }).catch(() => {});
    },
    onSubmit(){
      console.log(this.$refs.tree.getCheckedNodes());
      if(!this.form.roleName){
        this.$message.error('请输入角色名称');
        return;
      }
      let menuIds = []
      this.$refs.tree.getCheckedNodes().forEach(e=>menuIds.push(e.id))
      console.log('menuIds',menuIds)
      if(menuIds.length == 0){
        this.$message.error('请至少为角色添加一个菜单权限');
        return;
      }
      this.addLoading = true;
      this.form.shopId = this.shop.shopId
      this.form.menuIds = menuIds;
      update(this.form).then(res=>{
        console.log('添加角色',res)
        if(res.code == 200){
          this.$message({
            message:res.message,
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.replace({
              path:'/systemSetting/roleManage'
            })
            this.addLoading = false;
          },1000)
        }else{  
          this.$message.error(res.message);
          this.addLoading = false;
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>

</style>